import { memo, useCallback } from 'react';
import { HotspotCompactWrapper } from './styled';
import {
  I_CompactInteraction,
  I_Hotspot,
  I_Scene,
} from '@containers/Home/types';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { Tooltip } from 'antd';
import { isServer } from '@utils/helper';

const classIcon = typeHotspot => {
  switch (typeHotspot) {
    case 'point':
      return 'map-marker-alt';
    case 'article':
      return 'newspaper';
    case 'link':
      return 'link';
    case 'sound':
      return 'waveform';
    case 'video':
      return 'video';
    case 'image':
      return 'images';
    case 'product':
      return 'tshirt';
    default:
      return '';
  }
};

interface Props {
  hotspot: I_Hotspot;
  listScene: I_Scene[];
  languageSelected: string;
  onClick: (value) => void;
}

const HotspotCompact = memo(
  ({ hotspot, listScene, onClick, languageSelected }: Props) => {
    // eslint-disable-next-line react-hooks/exhaustive-deps
    const LANGUAGES = isServer ? {} : window.languages || {};

    const [, setTourEventState] = useTourEventState('state');

    const onRenderTitle = useCallback(
      (item: I_CompactInteraction) => {
        switch (item.type) {
          case 'point':
            return item.config?.target_scene_id
              ? listScene.find(
                  scene => scene.id === item.config?.target_scene_id,
                )?.title
              : '';
          case 'article':
            return LANGUAGES[languageSelected]?.hotspotCompactArticle;
          case 'image':
            return LANGUAGES[languageSelected]?.hotspotCompactImage;
          case 'link':
            if (item.config?.url?.toLowerCase().includes('pdf'))
              return LANGUAGES[languageSelected]?.hotspotCompactPDF;
            return LANGUAGES[languageSelected]?.hotspotCompactLink;
          case 'video':
            return LANGUAGES[languageSelected]?.hotspotCompactVideo;
          default:
            return '';
        }
      },
      [LANGUAGES, languageSelected, listScene],
    );

    return (
      <HotspotCompactWrapper
        onMouseEnter={() => {
          setTourEventState(prev => ({
            ...prev,
            descriptionHotspotSelected: hotspot.post?.content || undefined,
          }));
        }}
        onTouchStart={() => {
          setTourEventState(prev => ({
            ...prev,
            descriptionHotspotSelected: hotspot.post?.content || undefined,
          }));
        }}
        onMouseLeave={() => {
          setTourEventState(prev => ({
            ...prev,
            descriptionHotspotSelected: undefined,
          }));
        }}
        onTouchEnd={() => {
          setTourEventState(prev => ({
            ...prev,
            descriptionHotspotSelected: undefined,
          }));
        }}
      >
        <div className="compactHotspotWrapper">
          <div className="--mainMarker"></div>
          <div className="--revealMarkers">
            {hotspot?.config?.compact?.length &&
              hotspot?.config?.compact.map((item, index) => (
                <Tooltip
                  showArrow={false}
                  title={() => onRenderTitle(item)}
                  key={index}
                >
                  <div
                    className="--itemDetailMarker"
                    onClick={() => onClick(item)}
                    onTouchStart={() => onClick(item)}
                  >
                    <i className={`fas fa-${classIcon(item.type)}`} />
                  </div>
                </Tooltip>
              ))}
          </div>
        </div>
      </HotspotCompactWrapper>
    );
  },
);

export default HotspotCompact;
